import React from 'react';
import { Container } from 'react-bootstrap';
import img from "../assets/AboutIslam/brief_info.webp";
import { Link } from "react-router-dom";

const BriefIntroduction = () => {
    return (
        <section>
            <div style={{ marginBottom: "30px" }}>
                <div style={{
                    paddingLeft: "16px",
                    paddingRight: "16px",
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center"
                }}>
                    <img src={img} style={{ width: "1920px", borderRadius: "16px" }} />
                </div>
                <Container>
                    <div style={{
                        fontSize: "24px",
                        marginBottom: "16px",
                        marginTop: "20px",
                        borderRadius: "12px",
                        height: "32px",
                        backgroundColor: "#136833",
                        padding: "30px",
                        display: "flex",
                        justifyContent: "flex-start",
                        alignItems: "center",
                        boxShadow: "0 1px 20px 0 gray, 0 1px 20px 0 #136833"
                    }}>
                        <Link to="/AboutIslam">About Islam&nbsp;&nbsp;&nbsp;</Link>{">"}
                        <span>&nbsp;&nbsp;&nbsp;Brief Introduction</span>
                    </div>
                    <div style={{
                        borderRadius: "12px",
                        backgroundColor: "aliceblue",
                        padding: "20px",
                        textAlign: "justify",
                        fontSize: "16px",
                        marginTop: "20px",
                        boxShadow: "0 1px 20px 0 gray, 0 1px 20px 0 #136833",
                        marginBottom: "20px"
                    }}>
                        <h1 style={{}}>Islam</h1>
                        <p>The only and true religion that has been revealed and sent to all prophets is Islam. In the 19th Ayah of Srah li-Imrn, Allh Ta'l says: Verily, the only true religion in the sight of Allh is Islam.</p>
                        <p>Islam was first revealed to dam (alayhis salm) and all the prophets one after the other, and finally, it was revealed to our Prophet Muhammad Mustaf (sallallhu alayhi wa sallam) to reach perfection.</p>
                        <p>Our Nab, Muhammad (sallallhu alayhi wa sallam) was sent as a prophet to the entire mankind and the Jinn to convey this true religion. For this reason, he is the last prophet, and his Shariah (religion) is the final Shariah.</p>
                        <p>The points which all the prophets commonly conveyed are as follows:</p>
                        <p>Allh Tal is one. He is described with perfect attributes and free from all faults and defects.</p>
                        <p>Offering Ibdahs (acts of worship) to Allh Tal with Ikhls (sincerity), and striving to attain closeness to His Rahmah (mercy) through righteous deeds.</p>
                        <p>Believing that Allh Tals angels, His prophets, scriptures that He revealed to His prophets, Qadar and Qadh (The Divine Decree and Will), the Day of Qiymah, resurrection, Jannah, Jahannam, and other facts about the Hereafter are all Haqq (true).</p>
                        <p>Obeying the commands and prohibitions of Allh, Believing that obedience to the prophets, Kalimah Shahdah, Salh, fasting, Hajj and Zakt are Fard and they need to be practised.</p>
                        <p>Reciting the Book of Allh, seeking closeness to Him through Dus (supplication), Dhikr (remembrance of Allh) and other Nfilah (supererogatory) Ibdahs.</p>
                        <p>Abstaining from bad Akhlq (character), and adorning oneself with good Akhlq. Knowing that Nikh (wedlock) is Halal (lawful) and Zin (adultery) is Haram (prohibited) and acting accordingly.</p>
                        <p>Knowing that Nikh (wedlock) is Halal (lawful) and Zin (adultery) is Haram (prohibited) and acting accordingly. Enjoining people with justice, generosity, Sidq (truthfulness), Istiqmah (steadfastness), loyalty and fulfilment of trust. Forbidding lies, abusing people and violating their rights.</p>
                        <p>l al-Kalimatullh: Striving to spread the Exalted Speech of Allh and the Sunnah of His Prophet (sallallhu alayhi wa sallam). Islm is to obey Allh  by acknowledging the messages communicated by Raslullh, the Prophet of Allh     with the tongue and confirming them in the heart.</p>
                        <div>
                            <p>The essential articles of Islm are fve. In other words, Islm is based upon five pillars:</p>
                            <li>To pronounce Kalimah Al-Shahdah, the first essential article of Islm, reads as follows:                                                        Ash-hadu an l ilha illallh, wa ash-hadu anna Muhammadan abduh wa Rasluh. Translation: I bear witness that there is none worthy of worship except Allh and I bear witness that Muhammad     is His servant and prophet.</li>
                            <li>To perform Salh,</li>
                            <li> To give Zakh,</li>
                            <li>To fast in the month of Ramadhn,</li>
                            <li>To go for Hajj. One who fulfills the conditions of Islm is called a Mumin or Muslim. Whoever denies any one of these articles goes out of the fold of Islm.</li>
                        </div>
                    </div>
                </Container>
            </div>
        </section>
    );
}

export default BriefIntroduction;